/* General */
* {
  font-size: 100%;
  margin: 0;
  padding: 0;
  caret-color: transparent;
}

body {
  background-image: url("./assets/yellow_wood_oak.jpg");
}

a,
a:hover {
  color: inherit;
  text-decoration: none;
}

/* Navbar */
.Navbar {
  display: flex;
  justify-content: space-between;
  font-size: 6rem;
  color: rgb(221, 198, 89);
  background-image: linear-gradient(
    170deg,
    rgb(136, 0, 21) 25%,
    rgb(184, 96, 109)
  );
  background-color: rgb(136, 0, 21);
  margin: 15px 0em 0.5em;
  border-top: 5px ridge rgb(221, 198, 89);
  border-bottom: 5px ridge rgb(221, 198, 89);
  padding: 0.3em 1em 0.35em;
  box-shadow: 0px 10px 9px rgba(36, 36, 36, 0.8);
}

.LeftNav h1 {
  text-shadow: 3px 2px black;
}

.LoginNav {
  font-size: 1.2rem;
  width: fit-content;
  position: relative;
  left: 25rem;
  text-shadow: 1.5px 1px black;
}

.LoginNav:hover {
  color: rgb(251, 231, 130);
  text-decoration: underline;
}

.RightNav {
  display: flex;
  justify-content: flex-start;
  font-size: 5rem;
  text-align: center;
  margin-top: 0.25em;
}

.RightNav h3 {
  font-size: 2rem;
  margin-top: -0.75em;
}

.NavButton:hover {
  /* TODO add hover animation */
}

.NavButton + .NavButton {
  margin-left: 0.5em;
}

/* Item Page */
.ItemPage {
  /* background-image: url("./assets/yellow_wood_oak.jpg"); */
  /* background-position: fixed; */
  padding-bottom: 5em;
}

/* Item */
.Item {
  display: flex;
  font-size: 1.5rem;
  text-align: center;
  background-color: moccasin;
  width: 70%;
  margin: 0 auto;
  margin-top: 1em;
  border-radius: 20px;
  padding: 2em;
  box-shadow: 8px 9px 5px -4px rgb(27, 27, 27),
    -8px 9px 5px -4px rgb(27, 27, 27);
  transition: 0.375s;
}

.Item:hover {
  cursor: pointer;
  width: 80%;
  margin-top: 1.5em;
  margin-bottom: 1.5em;
  padding: 2.5em;
  box-shadow: 12px 20px 15px -4px rgb(8, 8, 8),
    -12px 20px 15px -4px rgb(8, 8, 8);
}

.Item section {
  margin: auto;
}

.Item section h2 {
  font-size: 2rem;
}

.Item img {
  height: 200px;
  width: 200px;
}

